/* Basic Theme */
// colors
$background: #000;
$color: #fff;

// fonts
$font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
$font-weight: 300;
$phone-text-size: 12pt;
$text-size: 16pt;
$line-height: 1.5;

/* Responsive Sizing */
//max
$max-phone: 419px;
$max-tablet: 767px;
$max-laptop: 1023px;
$max-desktop: 1199px;
//min
$min-phone: 420px;
$min-tablet: 768px;
$min-laptop: 1024px;
$min-desktop: 1200px;

// sunrise colors
$sunrise-purple: rgb(69, 56, 91);
$sunrise-pink: rgb(172, 116, 143);
$sunrise-orange: rgb(255, 140, 76);
$sunrise-yellow: rgb(254, 213, 113);
$sunrise-white: rgb(255, 244, 188);

$futures-light-green: #9AB87A;
$futures-dark-green: #708B75;
$futures-blue: #444B6E;
$futures-brown: #695958;
