#futures {
  background: linear-gradient(to bottom, rgb(0, 0, 0), $sunrise-purple, $sunrise-pink);
}

#aemp {
  .left {
    background: linear-gradient(to bottom, $sunrise-pink, $sunrise-orange);
  }
  .right {
    background-image: url('../media/aemp.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

#ajl {
  .left {
    background-image: url('../media/ajl.png');
    background-position: right;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .right {
    background: linear-gradient(to bottom, $sunrise-orange, $sunrise-yellow);
  }
}

#proj1619 {
  .left {
    background: linear-gradient(to bottom, $sunrise-yellow, $futures-light-green);
  }
  .right {
    background-image: url('../media/1619.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

#tech {
  background: linear-gradient(to bottom, $futures-light-green, $futures-dark-green, $futures-blue);
}
