@import './styles/variables';
@import './styles/placeholders';

div, h1, h2, p, button {
  margin: 0;
  padding: 0;
}

#App {
  display: grid;
  background-color: $background;
  color: $color;
  font-size: $phone-text-size;
  line-height: $line-height;

  @media screen and (min-width: $min-tablet) {
    font-size: $text-size;
  }

  height: 100vh;
  max-height: 100vh;
  width: 100vw;
  max-width: 100vw;
  @extend %scroll-snap;
  scroll-behavior: smooth;
}

h1 {
  @extend %display-text;
  font-weight: $font-weight;
}

button {
  @extend %display-text;
  transition: .5s;
  display: inline-block;
  background-color: $background;
  color: $color;
  border: 1px solid $color;
  box-shadow: 0;
  padding: 2.5vh 5vw;

  &:hover {
    background-color: $color;
    color: $background;
    cursor: pointer;
  }
}

.section {
  @extend %perfect-centering;
  height: 100%;
  min-height: 100vh;
  min-width: 100vw;
  scroll-snap-align: start;
}

.section-container {
  @extend %perfect-centering;
  grid-gap: 5vh;
  padding: 5vh 0;
  width: 80%;
  min-height: 80%;
}

.text-container {
  display: grid;
  grid-gap: $phone-text-size;
  width: 100%;
  max-width: 800px;

  @media screen and (min-width: $min-tablet) {
    grid-gap: $text-size;
  }

  p {
    text-align: justify;
  }
}

.split-section {
  @extend .section;

  @media screen and (min-width: $min-tablet) {
    grid-template-columns: 1fr 1fr;
  }
}

.left {
  @extend %half-section;

  @media screen and (max-width: $max-tablet) {
    .text-container p {
      text-align: right;
    }
  }
}

.right {
  @extend %half-section;

  @media screen and (max-width: $max-tablet) {
    .text-container p {
      text-align: left;
    }
  }
}

.red-text {
  color: #900;
}

@import './styles/intro';
@import './styles/history';
@import './styles/present';
@import './styles/futures';
@import './styles/sources';
