@import url(https://fonts.googleapis.com/css?family=Baskervville&display=swap);
body {
  margin: 0;
  font-family: 'Baskerville', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* Basic Theme */
/* Responsive Sizing */
#App {
  overflow: scroll;
  -ms-scroll-snap-type: y proximity;
      scroll-snap-type: y proximity; }
  @media screen and (min-width: 768px) and (min-height: 768px) {
    #App {
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory; } }

h1, button {
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
  font-weight: 300;
  font-size: 12pt;
  letter-spacing: 1.5px;
  text-transform: lowercase; }
  @media screen and (min-width: 768px) {
    h1, button {
      font-size: 14pt; } }

.left, .right, .section, .split-section, .section-container {
  display: grid;
  place-items: center center;
  align-content: center; }

.left, .right {
  grid-gap: 5vh;
  width: 100%;
  min-height: 100vh; }
  .left .text-container, .right .text-container {
    width: 80%; }

div, h1, h2, p, button {
  margin: 0;
  padding: 0; }

#App {
  display: grid;
  background-color: #000;
  color: #fff;
  font-size: 12pt;
  line-height: 1.5;
  height: 100vh;
  max-height: 100vh;
  width: 100vw;
  max-width: 100vw;
  scroll-behavior: smooth; }
  @media screen and (min-width: 768px) {
    #App {
      font-size: 16pt; } }

h1 {
  font-weight: 300; }

button {
  -webkit-transition: .5s;
  transition: .5s;
  display: inline-block;
  background-color: #000;
  color: #fff;
  border: 1px solid #fff;
  box-shadow: 0;
  padding: 2.5vh 5vw; }
  button:hover {
    background-color: #fff;
    color: #000;
    cursor: pointer; }

.section, .split-section {
  height: 100%;
  min-height: 100vh;
  min-width: 100vw;
  scroll-snap-align: start; }

.section-container {
  grid-gap: 5vh;
  padding: 5vh 0;
  width: 80%;
  min-height: 80%; }

.text-container {
  display: grid;
  grid-gap: 12pt;
  width: 100%;
  max-width: 800px; }
  @media screen and (min-width: 768px) {
    .text-container {
      grid-gap: 16pt; } }
  .text-container p {
    text-align: justify; }

@media screen and (min-width: 768px) {
  .split-section {
    grid-template-columns: 1fr 1fr; } }

@media screen and (max-width: 767px) {
  .left .text-container p {
    text-align: right; } }

@media screen and (max-width: 767px) {
  .right .text-container p {
    text-align: left; } }

.red-text {
  color: #900; }

