#history {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, .5), rgba(0, 0, 0, 1)), url('../media/shellmound.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#indigenous {
  .left {
    .text-container p:first-child {
      text-align: right;
    }
  }

  .right {
    background-image: radial-gradient(ellipse, rgba(0, 0, 0, .6), rgba(0, 0, 0, .6)), url('../media/ohlonemap.jpg');
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

#chinese {
  .left {
    background-image: url('../media/chinese.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

#redlining .explore {
  display: grid;

  img {
    width: 100%;
  }

  @media screen and (min-width: $min-tablet) {
    grid-gap: 5vw;
    grid-template-columns: 1fr 1fr;
    place-items: center center;

    img {
      width: 25vw;
    }
  }
}
